import { useSetRecoilState } from 'recoil';
import styled from 'styled-components';
import { IconContext } from "react-icons";
import { GrClose } from "react-icons/gr";
import { Category, ITodo, toDoState } from '../atoms';

const ButtonWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
`;

const CategoryButton = styled.button`
  border: 1px solid white;
  border-radius: 10px;
  color: white;
  background-color: transparent;

  &:hover {
    opacity: 0.4;
  }
`;

const DeleteBtn = styled.button`
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  background-color: transparent;
  border: none;
  
  &:hover,
  &:active {
    opacity: 0.4;
  }
`;

const TodoItem = styled.li`
  width: 25%;
  height: 120px;
  border: 1px solid rgba(255, 255, 255, .4);
  border-radius: 15px;
  margin-bottom: 15px;
  padding: 15px;

  P {
    margin-top: 15px;
  }
`;

function ToDo({ text, category, id }: ITodo) {
  const setTodos = useSetRecoilState(toDoState);

  const onClick = (event:React.FormEvent<HTMLButtonElement>) => {
    const {
      currentTarget: { name }
    } = event;

    setTodos(prevTodo => {
      const targetIndex = prevTodo.findIndex(todo => todo.id === id);
      const oldTodo = prevTodo[targetIndex];
      const newTodo = { text, id, category: +name as Category };

      const resultFront = [...prevTodo].splice(0, targetIndex);
      const resultBack = [...prevTodo].splice(targetIndex + 1);
      
      const updateTodos = [...resultFront, newTodo, ...resultBack];
      localStorage.setItem('todos', JSON.stringify(updateTodos));
      return updateTodos;
      
      // immutability () 
    });
  }

  const deleteClick = () => {
    setTodos(prevTodo => {
      const deletTodos = prevTodo.filter((todo) => todo.id !== id);
      localStorage.setItem('todos', JSON.stringify(deletTodos));
      return deletTodos;
    });
  }

  console.log('ToDo');

  return (
    <TodoItem>
      <ButtonWrapper>
        <div>
          {
            category !== Category.DOING && (
              <CategoryButton 
                type="button" 
                name={Category.DOING + ""}
                onClick={onClick}>Doing
              </CategoryButton>
            )
          }
          {
            category !== Category.TO_DO && (
              <CategoryButton 
                type="button" 
                name={Category.TO_DO + ""}
                onClick={onClick}>To Do
              </CategoryButton>
            )
          }
          {
            category !== Category.DONE && (
              <CategoryButton 
                type="button" 
                name={Category.DONE + ""} 
                onClick={onClick}>Done
              </CategoryButton>
            )
          }
        </div>
        {
          <DeleteBtn type="button" onClick={deleteClick}>
            <IconContext.Provider value={{ color: "white" , size: "14px"}}>
              <GrClose color="white" />
            </IconContext.Provider>
          </DeleteBtn>
        }
      </ButtonWrapper>
      <p>{text}</p>
    </TodoItem>
  );
}

export default ToDo;

// [       ] 
//       
// callback     .
// ex) onClick={clickFunc}
//     -> onClick={() => clickFunc(val)}

// [ findIndex() ]
// findIndex(predicate: (value: T, index: number, obj: T[]) => unknown, thisArg?: any): number;

// [       . ]
//    mutate     .
//  const newTodos = prevTodos  
// prevTodos   newTodos   newTodos  prevTodos   mutate .
//    object array 
//     spread  .
//   Object.assign()      .

//  splice()       map      .

//  Mutate let a = 1;  a = 2   a   .
//     ()       .


// [ recoil - selector ]
//  : Selector derived state ( state) .
// drived state,
// 1. state  
// 2.     
// 3.   .

//   ()
// - selector  atom output  .
//     
//   state   , state output    selector .
// - selector  state   return  .
// - useRecoilValue() atom output, selector output   .

//   ()
// - state (render)  2 
//   1. state output   , useRecoilValue() atom  
//   2. state output    , useRecoilValue() selector  

// -  selector  state   
//          state output  .
